import React from 'react';
import { Link } from 'react-router-dom';
import './home.scss';
import { Input,Icon,Row,Col } from 'antd'
import Surah from '../Surah/Surah';
import PropTypes from 'prop-types';
import Navbar from '../Navbar/Navbar';
import chapters from '../../jsons/quran/chapters.json';
import _ from 'underscore';

const pt = {
  quranReducer: PropTypes.object,
  actions: PropTypes.object,
  appReducer: PropTypes.object
}

class HomePage extends React.Component{

  constructor(){
    super();
    
    this.state = {
      chapters: chapters
    };

    console.log(chapters)
  }
  componentDidMount(){
    window.scrollTo(0,0)
  }


  search = (e) => {
    _.mixin({
      like: function(text, likeExpr) {
        var regex = new RegExp(text.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, "\\$&"), "i");
        return regex.test(likeExpr);
      }
    });

    this.setState({chapters: _.filter(chapters, o => {
        return _.like(e.target.value, o.name_simple);
      })
    });
    
  }
  
  render = () => {
    const {appReducer} = this.props;
    return (
      <div className="home">
        <Navbar 
            header="Hifze Quran" 
            icon="setting" 
            forward="/settings"
        />
        <Input
          className="search-bar"
          placeholder="Search Surah"
          prefix={<Icon type="search" style={{ color: '#A3A4A5' }} />}
          onChange={this.search.bind(this)}
        />
        <Row gutter={16}>
          {
            this.state.chapters.map((surah,index)=>(
              <Col lg={4} xs={12} sm={8} className="surah-item" key={index}>
                <Surah surah={surah} settings={appReducer} />
              </Col>
            ))
          }
        </Row>
      </div>
    );
  }
}

HomePage.propTypes = pt;

export default HomePage;
