import React, { Component } from 'react';
import { Input, Row, Col, Button } from 'antd';

class HifzSettings extends Component {

    state = {
        repeatSize: 16
    }

    generateRepeaters = () => {
        let arr = []
        for(let i=1; i<= this.state.repeatSize; i++){
            arr.push(
                <Col span={6} key={i} style={{marginBottom:'15px'}}>
                        <Button 
                            onClick={()=>this.chanageVal(i)}
                            className={i == this.props.appSettings.hifzSize ? "speed-button active" : "speed-button"} 
                            block>
                            {i}x
                        </Button>
                </Col>
            )
        }
        return arr;
    }

    chanageVal(e) {
        this.props.actions.changeHifzSize(e)
    }

    render() {
        const text = "فَبِأَىِّ ءَالَآءِ رَبِّكُمَا تُكَذِّبَانِ";
        return (
            <div className="hifz-settings">
                <label className="label-set">Current Surrah</label>
                <label style={{ float: 'right' }} className="label-set">
                    Surah Bakarah
                </label>
                <hr />
                <Row gutter={8}>
                    <Col span={12}>
                        <label className="label-set">From verse</label>
                        <Input className="material-input" value={1} />
                    </Col>
                    <Col span={12}>
                        <label className="label-set">To verse</label>
                        <Input className="material-input" value={12} />
                    </Col>
                </Row>
                <hr/>
                <label className="label-set">Repeat</label>
                <Row gutter={12}>
                    {this.generateRepeaters()}
                </Row>
                
            </div>
        )
    }
}

export default HifzSettings;