import React, { Component } from 'react';
import { Modal,Button,Row,Col,Input } from 'antd';
import PropTypes from 'prop-types';
const pt = {
    actions: PropTypes.object,
    appReducer: PropTypes.object,
    visible: PropTypes.bool,
    handleOk: PropTypes.func,
    handleCancel: PropTypes.func,
    from: PropTypes.any,
    to: PropTypes.any,
    noOfAyah: PropTypes.number
}

class SettingsModal extends Component {
    state = {
        repeatSize: 16
    }

    chanageVal(e) {
        this.props.actions.changeHifzSize(e)
    }

    generateRepeaters = () => {
        let arr = []
        for(let i=1; i<= this.state.repeatSize; i++){
            arr.push(
                <Col span={6} key={i+"rand"} style={{marginBottom:'15px'}}>
                        <Button 
                            onClick={()=>this.chanageVal(i)}
                            className={i == this.props.appReducer.hifzSize ? "speed-button active" : "speed-button"} 
                            block>
                            {i}x
                        </Button>
                </Col>
            )
        }
        return arr;
    }
    render() {
        return (
            <Modal
                title="Hifz Settings"
                visible={this.props.visible}
                onOk={this.props.handleOk}
                onCancel={this.props.handleCancel}
            >
                <div className="hifz-settings">
                    
                    <Row gutter={8}>
                        <Col span={12}>
                            <label className="label-set">From verse</label>
                            <Input type="number" onChange={(e)=>this.props.changeVal('from',e.target.value)} className="material-input" value={this.props.from} />
                        </Col>
                        <Col span={12}>
                            <label className="label-set">To verse</label>
                            <Input type="number" max={this.props.noOfAyah} onChange={(e)=>this.props.changeVal('to',e.target.value)} className="material-input" value={this.props.to} />
                        </Col>
                    </Row>
                    <hr />
                    <label className="label-set">Repeat</label>
                    <Row gutter={12}>
                        {this.generateRepeaters()}
                    </Row>

                </div>

            </Modal>
        )
    }
}

SettingsModal.propTypes = pt;

export default SettingsModal;