import React from 'react';
import { Row, Col } from 'antd';
import './ayah-arabic.scss';
import Word from './Word';

export const AyahArabic = ({ text, number, appReducer,ayah,playWord,playVerse }) => {
    console.log()
    return (
        window.screen.width > 768 ?
            <Row gutter={2}
                style={{
                    fontSize: appReducer.reciteFontSize + "px",
                    lineHeight: (appReducer.reciteFontSize * 2) + "px"
                }}
                className="arabic-ayah">
                <Col xs={2} lg={24} sm={24} className="number">
                    {number}
                </Col>
                <Col xs={22} lg={24} sm={24} className="text">
                    {ayah.words.map((w,i)=>(
                        <Word playWord={(e)=>playWord(e)} key={i} word={w} />
                    ))}
                    <p>{ayah.translations[0].text}</p>
                    <a className="play-but" onClick={()=>playVerse(ayah)}>
                        Play verse
                    </a>
                </Col>
            </Row>
            :
            <Row gutter={2}
                style={{
                    fontSize: appReducer.reciteFontSize + "px",
                    lineHeight: (appReducer.reciteFontSize * 2) + "px"
                }}
                className="arabic-ayah">
                <Col xs={22} lg={24} sm={24} className="text">
                    {ayah.words.map((w,i)=>(
                        <Word playWord={(e)=>playWord(e)} key={i} word={w} />
                    ))}
                    <p>{ayah.translations[0].text}</p>
                    <a className="play-but" onClick={()=>playVerse(ayah)}>
                        Play verse
                    </a>
                </Col>
                <Col xs={2} lg={24} sm={24} className="number">
                    {number}
                </Col>
            </Row>
    )
}
