import * as types from '../constants/actionTypes';
import objectAssign from 'object-assign';

const mode = localStorage.getItem("mode");
const reciteFontSize = localStorage.getItem("reciteFontSize")
const hifzSize = localStorage.getItem("hifzSize");
const reciter = localStorage.getItem("reciter");
const translation = localStorage.getItem("translation");
const lang = localStorage.getItem("lang");

const initialState = {
    mode: typeof mode === 'object' ?   "recite" : mode,
    reciteFontSize: typeof reciteFontSize === 'object' ?   20 : reciteFontSize,
    hifzSize: typeof hifzSize === 'object' ?   3 : hifzSize,
    reciter: typeof reciter === 'object' ?   {reciter_name_eng: "Mishari Rashid al-`Afasy",id: 7} : JSON.parse(reciter),
    translation: typeof translation === 'object' ? {
        "id": 20,
        "author_name": "Sahih International",
        "slug": "en-sahih-international",
        "name": "Sahih International",
        "language_name": "English"
        } : JSON.parse(translation),
    lang: 'en'//typeof translation === 'object' ? 'en' : lang
}

export const appReducer = (state = initialState,action={}) => {
    let newState = {};
    switch(action.type){
        case types.CHANGE_MODE:{
            newState = objectAssign({}, state);
            newState.mode = action.payload;
            localStorage.setItem("mode",action.payload);
            return newState;
        }
        case types.CHANGE_RECITE_FONT_SIZE:{
            newState = objectAssign({}, state);
            newState.reciteFontSize = action.payload;
            localStorage.setItem("reciteFontSize",action.payload);
            return newState;
        }
        case types.CHANGE_HIFZ_SIZE:{
            newState = objectAssign({}, state);
            newState.hifzSize = action.payload;
            localStorage.setItem("hifzSize",action.payload);
            return newState;
        }
        case types.CHANGE_RECITER:{
            newState = objectAssign({}, state);
            newState.reciter = action.payload;
            localStorage.setItem("reciter",JSON.stringify(action.payload));
            return newState;
        }
        case types.CHANGE_TRANSLATIONS:{
            newState = objectAssign({}, state);
            newState.translation = action.payload;
            localStorage.setItem("translation",JSON.stringify(action.payload));
            return newState;
        }
        default:{
            return state
        }
    }
}