/* eslint-disable import/no-named-as-default */
import { NavLink, Route, Switch } from "react-router-dom";

import HomePage from "./Home/HomePage";
import NotFoundPage from "./NotFoundPage";
import PropTypes from "prop-types";
import React from "react";
import { hot } from "react-hot-loader";
import MainContainer from "../MainContainer.js";
import Navbar from "./Navbar/Navbar";
import Settings from './Settings/Settings';
import Recite from './Recite/Recite';
import Hifz from './Hifz/Hifz';

// This is a class-based component because the current
// version of hot reloading won't hot reload a stateless
// component at the top-level.

class App extends React.Component {

  render() {
    const activeStyle = { color: 'blue' };
    return (
      <div className="container-fluid">
        <section className="app-area">
          <Switch>
            <Route exact path="/" component={MainContainer(HomePage)} />
            <Route path="/settings" component={MainContainer(Settings)} />
            <Route path="/recite/:surah" component={MainContainer(Recite)} />
            <Route path="/hifz/:surah" component={MainContainer(Hifz)} />
            <Route component={NotFoundPage} />
          </Switch>
        </section>
      </div>
    );
  }
}

App.propTypes = {
  children: PropTypes.element
};

export default hot(module)(App);
