import React, {Component} from 'react';
import './surah.scss';
import {Link} from 'react-router-dom';
import PropTypes from 'prop-types';


const pt = {
    surah: PropTypes.object,
  }
class Surah extends Component{
    render(){
        const {surah,settings} = this.props;
        return(
            <div>
                <Link className="surah" to={"/"+settings.mode+"/"+surah.chapter_number}>
                    <div className="english-name"> &nbsp; {surah.name_simple}</div>
                    <div className="number">{surah.chapter_number}</div>
                    <div className="arabic-name">{surah.name_arabic}</div>
                    <div className="english-meaning">{surah.translated_name.name}</div>
                    <div className="rev-type">{surah.revelation_place}</div>
                    <div className="ayahs">Verses {surah.verses_count}</div>
                </Link>
            </div>
        )
    }
}

Surah.propTypes = pt;

export default Surah;