import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Navbar from '../Navbar/Navbar';
import { Tabs } from 'antd';
import ReciteSettings from './ReciteSettings';
import HifzSettings from './HifzSettings';
const TabPane = Tabs.TabPane;

const pt = {
    appReducer:PropTypes.object,
    actions: PropTypes.object
}

class Settings extends Component{

    changeMode(e){
        this.props.actions.changeMode(e)
    }
    componentDidMount(){
        this.props.actions.getReciters();
        this.props.actions.getOptions('translations','GET_TRANSLATIONS')
    }
    render = () => {
        const {appReducer,actions,quranReducer} = this.props
        return(
            <div>
                <Navbar 
                    header="Surahs"  
                    back={true} 
                    backward="/"
                />
                <div className="settings">
                <Tabs defaultActiveKey={appReducer.mode} onChange={this.changeMode.bind(this)}>
                    <TabPane tab="Recite" key="recite">
                        <ReciteSettings  
                            appSettings = {appReducer}
                            actions={actions}
                            quranReducer = {quranReducer}
                        />
                    </TabPane>
                    <TabPane tab="Hifz" key="hifz">
                        <HifzSettings 
                            appSettings = {appReducer}
                            actions={actions}
                        />
                    </TabPane>
                    <TabPane tab="Ilim" key="ilim">Content of Tab Pane 3</TabPane>
                    <TabPane tab="Learn" key="learn">Content of Tab Pane 3</TabPane>
                    <TabPane tab="Listen" key="listen">Content of Tab Pane 3</TabPane>
                </Tabs>
                </div>
            </div>
        )
    }
}

Settings.propTypes = pt;
export default Settings;