import objectAssign from 'object-assign';
import { cloneDeep } from "lodash";
import quran from '../jsons/quran/uthmani.json';

const initialState = {
    quran: quran.data.surahs
}

export const quranReducer = (state = initialState,action={}) => {
    let newState = {};
    switch(action.type){
        case 'GET_SURAH': {
            newState = cloneDeep(state);
            if(action.payload.type === 'initial'){
                newState.currentSurah = action.payload.data;
            } else {
                newState.currentSurah.verses = newState.currentSurah.verses.concat(action.payload.data.verses);
                newState.currentSurah.meta = action.payload.data.meta
            }
            
            return newState;
        }
        case 'GET_RECITERS':{
            newState = objectAssign({},state);
            newState.reciters = action.payload;
            return newState;
        }
        case 'GET_TRANSLATIONS':{
            newState = objectAssign({},state);
            newState.translations = action.payload;
            return newState;
        }
        case 'DELETE_CURRENT_SURAH':{
            newState = objectAssign({},state);
            delete newState.currentSurah
            return newState;
        }
        default:{
            return state
        }
    }
}