import React, { Component } from 'react';
import Navbar from '../Navbar/Navbar';
import { AyahArabic } from '../Ayah/AyahArabic/AyahArabic';
import objectAssign from 'object-assign'

class Recite extends Component {
    constructor(){
        super();
        this.state = {
            currentWord: null,
            isPlaying: false
        }
    }
    componentDidMount() {
        this.props.actions.getSurah(
            this.props.match.params.surah,
            this.props.appReducer.reciter.id,
            this.props.appReducer.translation,
            this.props.appReducer.lang,
            this.props.quranReducer.currentSurah == undefined ? null : this.props.quranReducer.currentSurah.meta
        )
        // window.addEventListener('scroll', function (event) {
        //     console.log(event)
        // });
        document.addEventListener('scroll', this.trackScrolling);
        window.scrollTo(0,0)
    }

    excludeBismillah(quran, surahNumber) {
        let sr = objectAssign({}, quran[parseInt(surahNumber) - 1])
        //sr.ayahs[0].text = sr.ayahs[0].text.substr(39)
        return sr;
    }

    scrollHandler(e) {
        console.log(e.target)
    }
    isBottom(el) {
        return el.getBoundingClientRect().bottom <= window.innerHeight;
    }


    componentWillUnmount() {
        document.removeEventListener('scroll', this.trackScrolling);
        this.props.actions.deleteCurrentSurrah()
    }

    trackScrolling = () => {
        const wrappedElement = document.querySelector('.app-area');
        if (this.isBottom(wrappedElement)) {
            console.log('header bottom reached');
            this.props.actions.getSurah(
                this.props.match.params.surah,
                this.props.appReducer.reciter.id,
                this.props.appReducer.translation,
                this.props.appReducer.lang,
                this.props.quranReducer.currentSurah.meta
            );
            
            //window.scrollTo(0,window.innerHeight-40);
           // document.removeEventListener('scroll', this.trackScrolling);
        }
    };
    playWord(e){
        var audioBox = document.getElementById("audio_box");
        this.setState({
            currentWord: "https:" + e.audio.url
        })
        setTimeout(()=>{
            audioBox.play();
        },10)
    }

    playVerse(e){
        console.log(e);

        var audioBox = document.getElementById("audio_box");
        this.setState({
            currentWord: e.audio.url
        })
        setTimeout(()=>{
            audioBox.play();
        },10)
    }

    render = () => {
        const { quranReducer, appReducer } = this.props;
        const surah = this.excludeBismillah(quranReducer.quran, this.props.match.params.surah);
        return (
            <div className="recite" onScroll={this.scrollHandler}>
                <Navbar
                    header={surah.englishName}
                    backward="/"
                />
                <audio
                    id="audio_box"
                    src={this.state.currentWord}>
                </audio>
                <div className="ayahs">
                    {quranReducer.currentSurah && quranReducer.currentSurah.verses &&
                        quranReducer.currentSurah.verses.map((ayah, index) => (
                            <AyahArabic
                                key={index}
                                number={index + 1}
                                text={ayah.text_madani}
                                appReducer={appReducer}
                                ayah={ayah}
                                playWord={(e)=>this.playWord(e)}
                                playVerse={this.playVerse.bind(this)}
                            />
                        ))
                    }
                </div>
            </div>
        )
    }
}

export default Recite;
