import * as types from '../constants/actionTypes';
import Axios from 'axios';
import config from '../config.json';
import { message } from 'antd';
import _ from 'underscore';

export const changeMode = (mode) => (dispatch) =>{
    dispatch({
        type: types.CHANGE_MODE,
        payload: mode
    })
}

export const changeReciterFontSize = (val) => (dispatch) =>{
    dispatch({
        type: types.CHANGE_RECITE_FONT_SIZE,
        payload: val
    })
}

export const changeHifzSize = (val) => (dispatch) =>{
    dispatch({
        type: types.CHANGE_HIFZ_SIZE,
        payload: val
    })
}

export const getSurah = (sno,reciter,translations,lang,meta) => (dispatch) => {
    const msg = message.loading("Getting the verses..",0)
    Axios.get(`${config.api}/chapters/${sno}/verses?recitation=${reciter}&translations=${translations.id}&language=${lang}&text_type=words&page=${meta == null ? 1 : meta.next_page}`).then((resp)=>{
        dispatch({
            type: 'GET_SURAH',
            payload: {
                data: resp.data,
                type: meta == null ? 'initial': 'concat'
            }
        });
        msg()
    }).catch(()=>{
        msg();
        message.error("Error occures while fetching the verses")
    })
}

export const deleteCurrentSurrah = () => (dispatch) => {
    dispatch({
        type: 'DELETE_CURRENT_SURAH',
        payload:{}
    })
}


export const getReciters = () => (dispatch) => {
    Axios.get(`${config.api}/options/recitations`).then((resp)=>{
        console.log(resp.data)
        dispatch({
            type: 'GET_RECITERS',
            payload: resp.data.recitations
        })
    })
}


export const getOptions = (option,actionKey) => (dispatch) => {
    Axios.get(`${config.api}/options/${option}`).then((resp)=>{
        console.log(resp.data)
        dispatch({
            type: actionKey,
            payload: resp.data[option]
        })
    })
}


export const changeReciter = (data,reciters) => dispatch => {
    dispatch({
        type: types.CHANGE_RECITER,
        payload: _.find(reciters,(d)=>d.id === data)
    })
}

export const changeTranslations = (data,translations) => dispatch => {
    dispatch({
        type: types.CHANGE_TRANSLATIONS,
        payload: _.find(translations,(d)=>d.id === data)
    })
}
