import React from 'react';
import PropTypes from 'prop-types';
import { Popover } from 'antd';

const Word = ({ word, playWord }) => {
    return (
        <Popover content={word.translation ? word.translation.text : ""}>
            <span
                className="the-word"
                onClick={() => playWord(word)}
                style={{ letterSpacing: 2, display: 'inline-block' }}>
                {word.text_madani}
            </span>
        </Popover>

    );
};

Word.propTypes = {
    word: PropTypes.object
};

export default Word;