import React, { Component } from 'react';
import Navbar from '../Navbar/Navbar';
import { AyahArabic } from '../Ayah/AyahArabic/AyahArabic';
import objectAssign from 'object-assign';
import {Icon} from 'antd';
import './hifz.scss';
import SettingsModal from './SettingsModal';
class Hifz extends Component{

    constructor(){
        super();
        this.state = {
            from: 0, to: 0, visible: false
        }
    }
    componentDidMount(){
        window.scrollTo(0, 0);
        let count = this.props.quranReducer.quran[parseInt(this.props.match.params.surah)-1].ayahs.length;
        this.setState({
            from: 1,
            to: count,
            noOfAyah: count
        })
    }

    excludeBismillah(quran,surahNumber){
        let sr = objectAssign({},quran[parseInt(surahNumber)-1])
        //sr.ayahs[0].text = sr.ayahs[0].text.substr(39)
        return sr;
    }

    repeatAyah(index,ayah){
        let arr = []
        for(let i=0;i<this.props.appReducer.hifzSize;i++){
            arr.push(
                <AyahArabic 
                    key={'ayah_'+index+"_"+i} 
                    number={index+1} 
                    text={ayah.text} 
                    appReducer={this.props.appReducer}
                />
            )
        }
        return arr;
    }

    changeVal(type,val){
        if(type === 'from'){
            this.setState({
                from: val
            })
        } else if(type === 'to') {
            this.setState({
                to: val
            })
        }
    }

    printSurah(surah){
        let arr = [];
        let saphire = this.state.from - 1
        if(saphire >= 0){
            for(let i = saphire; i < this.state.to; i++  ){
                arr.push(this.repeatAyah(i,surah[i]))
            }
        }
        
        return arr
    }

    render = () => {
        const {quranReducer,appReducer,actions} = this.props;
        const surah = this.excludeBismillah(quranReducer.quran,this.props.match.params.surah);
        return(
            <div className="recite">
                <Navbar 
                    header={surah.englishName} 
                    backward="/"
                />
                <Icon type={"setting"} onClick={()=>this.setState({visible:true})} className="setting-icon top-nav" />

                <div className="ayahs">
                    {
                        this.printSurah(surah.ayahs)
                    }
                </div>
                <SettingsModal 
                    appReducer={appReducer}
                    actions={actions}
                    visible={this.state.visible}
                    from={this.state.from}
                    to={this.state.to}
                    noOfAyah={this.state.noOfAyah}
                    handleCancel={()=>this.setState({visible:false})}
                    changeVal={(type,val)=>this.changeVal(type,val)}
                />
            </div>
        )
    }
}

export default Hifz;
